.hero{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    width: 100%;
    padding-top: 30px;
    background-image: linear-gradient(to right,  #181823 60%, #000000cc 10%);

    @media screen and(min-width:768px){
        height: 450px;
        // padding-top: 80px;
    }
   
    @media screen and(min-width:1200px){
        padding-top: 80px;
    }
    
}
.hero_img-container{
    position: absolute;
    bottom: 0;
    left: 30px;
    // height: 300px;
    @media screen and(min-width:768px){
      
        bottom: 0;
        left: 140px; 
    }
    @media screen and(min-width:1200px){
      
        bottom: 0;
        left: 280px; 
    }
}
.hero_img{
    width: 200px;
    height: 250px;
    @media screen and (min-width:768px){
        width: 350px;
    height: 450px;
    }
}
.hero_text{
    position: absolute;
    bottom: 0;
    right: 5px; 
    z-index: 99;
    width: 200px;
    @media screen and(min-width:768px){
        bottom: 0px;
        left: 50%;
        width: 350px;
        height: 320px;
       
        }
    @media screen and(min-width:1200px){
        
    left: 520px;
    width: 390px;
    height: 320px;
   
    }
}
.color_text{
    color: #ffffff50;

}
.hero_title{
    font-size: 18px;
    font-weight: bold;
    line-height: 1.2;
    letter-spacing: 0.7;
    padding-bottom: 47px;
    @media screen and(min-width:768px){
        font-size: 36px;    
    }
    @media screen and(min-width:1200px){
        // font-size: 78px;    
    }
}
.hero_paragraph{
    font-size: 12px;
    @media screen and(min-width:1200px){
        font-size: 16px;    
    }
}